import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import Header from '../components/header'

const About = () => (
  <div className=" row page about-page">
    <Helmet
      title="About me, Kacper Cieciński Junior Web Developer"
      meta={[
        { name: 'description', content: 'Learn more about me, Kacper Cieciński' },
        { name: 'keywords', content: 'Kacper, Cieciński, Kacper Cieciński, Ciecinski, developer, junior developer,junior react developer, junior front-end developer,front-end,frontend,backend,junior backend developer,junior rails developer,React,Ruby on Rails,Javascript, HTML5, CSS, Politechnika Łódzka, Lodz University of Technology' },
      ]}
    />
      <h1>About Me</h1>
      <div className="col-md-6">
      <p>
        Hi, I'm Kacper Cieciński
      </p>
      <p className="my-desc">
        I'm a web developer with passion for programming since High School. <br/>
        I like to develop both frontend and backend, my favourite framework for front is React and for backend Ruby on Rails. <br/>
        I'm also a sophomore student in Lodz University of Technology majoring Computer Science.
      </p>
      </div>
    </div>
)

export default About



